@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-v14-latin_cyrillic-regular.woff2") format("woff2"), url("../fonts/montserrat-v14-latin_cyrillic-regular.woff") format("woff");
  font-weight: 400;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-v14-latin_cyrillic-500.woff2") format("woff2"), url("../fonts/montserrat-v14-latin_cyrillic-500.woff") format("woff");
  font-weight: 500;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-v14-latin_cyrillic-600.woff2") format("woff2"), url("../fonts/montserrat-v14-latin_cyrillic-600.woff") format("woff");
  font-weight: 600;
  font-display: swap; }

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: #2C2E3F;
  background-color: #ffffff; }

a {
  text-decoration: none;
  color: #2C2E3F; }
  a:hover {
    opacity: 0.7; }

img {
  max-width: 100%;
  height: auto; }

input,
textarea,
button {
  outline: none; }

input:not([type="radio"]):not([type="checkbox"]),
textarea,
button {
  appearance: none;
  border-radius: 0; }

textarea {
  resize: none; }

.blocked-scrolling {
  overflow: hidden; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

.container {
  min-width: 320px;
  margin: 0 auto; }

@media (min-width: 768px) {
  .container {
    min-width: 768px;
    padding-right: 185px;
    padding-left: 185px; } }

@media (min-width: 1024px) {
  .container {
    padding: 0 375px;
    max-width: 1920px; } }

.header {
  padding-top: 16px;
  padding-bottom: 13px;
  display: flex;
  justify-content: flex-end;
  align-items: center; }

.header__logo {
  margin-right: auto; }

.header__menu-btn {
  display: none; }

.header__link {
  margin-right: 40px;
  padding-top: 20px;
  padding-bottom: 20px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #141517; }
  .header__link:last-child {
    margin-right: 0; }

.header__link--red {
  color: #FE7865; }

.header__link--price {
  margin-right: 55px; }

.header__link--phone {
  position: relative;
  padding-left: 40px;
  padding-right: 14px;
  font-weight: 400;
  font-size: 18px;
  line-height: 32px;
  text-decoration: underline; }

.header__link--phone svg {
  position: absolute;
  left: 0;
  fill: none; }

@media (max-width: 1024px) {
  .header {
    padding: 0; }
  .header__logo {
    margin: 0;
    margin-right: auto;
    padding: 18px 22px 11px; }
  .header__menu-btn {
    display: block;
    position: relative;
    width: 72px;
    height: 50px;
    background-color: #FE7865; }
    .header__menu-btn::after {
      content: "";
      position: absolute;
      width: 22.5px;
      height: 1px;
      left: calc(50% - 11.25px);
      top: 24px;
      background-color: #ffffff;
      box-shadow: 0 6px 0 0 #ffffff, 0 -6px 0 0 #ffffff; }
  .header__icon {
    width: 44px;
    height: 17px; }
  .header__link {
    margin: 0;
    padding: 0;
    font-size: 0; }
  .header__link--price {
    margin: 0; }
  .header__link--phone {
    margin: 0; }
  .header__link--phone svg {
    width: 0;
    height: 0; } }

.hero {
  width: 100%;
  background-image: url(../img/hero-1x.jpg);
  background-repeat: no-repeat;
  background-size: cover; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .hero {
      background-image: url(../img/hero-2x.jpg); } }

.hero__container {
  padding-top: 184px;
  padding-bottom: 297px;
  display: flex;
  flex-direction: column;
  align-items: flex-end; }

.hero__title {
  margin: 0;
  padding: 0;
  max-width: 40%;
  margin-bottom: 28px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 600;
  font-size: 55px;
  line-height: 80px;
  letter-spacing: -1.10667px;
  color: #ffffff; }

.hero__description {
  margin: 0;
  padding: 0;
  max-width: 40%;
  margin-bottom: 34px;
  padding-right: 80px;
  font-family: Montserrat;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 170%;
  letter-spacing: -0.466667px;
  color: #ffffff; }

.hero__link {
  position: relative;
  padding: 30px 179px 30px 40px;
  margin-right: 130px;
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #FE7865; }

.hero__arrow {
  position: absolute;
  right: 38px;
  fill: none; }

@media (max-width: 1024px) {
  .hero__container {
    display: block; }
  .hero__title {
    text-align: center;
    max-width: 100%;
    margin: 0 0 0 0;
    font-size: 30px;
    line-height: 150%; }
  .hero__description {
    margin: 0;
    padding: 0;
    max-width: 100%;
    text-align: center; } }
